%heading {
  font-family: var(--font-display);
}

body {
  font-family: var(--font-primary);
  color: var(clr-grey-dark-2);
  font-weight: 300;
  line-height: 1.6;
}

.heading-1 {
  @extend %heading;
  font-size: 4.5rem;
  color: var(--clr-grey-light-1);
  line-height: 1;
}

.heading-2 {
  @extend %heading;
  font-size: 4rem;
  font-style: italic;
  line-height: 1;
  
  &--light {color: var(--clr-grey-light-1);}
  &--dark {color: var(--clr-grey-dark-1);}
}

.heading-3 {
  @extend %heading;
  font-size: 1.6rem;
  color: var(--clr-primary);
  text-transform: uppercase;
}

.heading-4 {
  @extend %heading;
  font-size: 1.9rem;

  &--light {color: var(--clr-grey-light-1);}
  &--dark {color: var(--clr-grey-dark-1);}
}

