@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,400i|Nunito:300,300i");
:root {
  --clr-primary: #c69963;
  --clr-primary-dark: rgb(178, 132, 81);
  --clr-secondary: rgb(16, 29, 44);
  --clr-grey-light-1: #f9f7f6;
  --clr-grey-light-2: #aaa;
  --clr-grey-dark-1: #54483a;
  --clr-grey-dark-2: #6d5d4b;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style-type: none;
}

a:link,
a:visited {
  text-decoration: none;
}

:root {
  --font-primary: "Nunito", sans-serif;
  --font-display: "Josefin Sans", sans-serif;
}

.heading-4, .heading-3, .heading-2, .heading-1 {
  font-family: var(--font-display);
}

body {
  font-family: var(--font-primary);
  color: var(clr-grey-dark-2);
  font-weight: 300;
  line-height: 1.6;
}

.heading-1 {
  font-size: 4.5rem;
  color: var(--clr-grey-light-1);
  line-height: 1;
}

.heading-2 {
  font-size: 4rem;
  font-style: italic;
  line-height: 1;
}
.heading-2--light {
  color: var(--clr-grey-light-1);
}
.heading-2--dark {
  color: var(--clr-grey-dark-1);
}

.heading-3 {
  font-size: 1.6rem;
  color: var(--clr-primary);
  text-transform: uppercase;
}

.heading-4 {
  font-size: 1.9rem;
}
.heading-4--light {
  color: var(--clr-grey-light-1);
}
.heading-4--dark {
  color: var(--clr-grey-dark-1);
}

.features {
  grid-column: center-start/center-end;
  margin: 15rem 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(27rem, 1fr));
  grid-column-gap: 6rem;
  grid-row-gap: 2rem;
  align-items: start;
}

.feature {
  display: grid;
  grid-template-columns: min-content auto;
  grid-column-gap: 2rem;
  grid-row-gap: 1rem;
}
.feature__icon {
  grid-row: 1/3;
  fill: var(--clr-primary);
  width: 4.5rem;
  height: 4.5rem;
  transform: translateY(-0.7rem);
}
.feature__text {
  grid-column: 2/-1;
  font-size: 1.7rem;
}

.footer {
  background-color: var(--clr-secondary);
  grid-column: full-start/full-end;
}

.gallery {
  background-color: blueviolet;
  grid-column: full-start/full-end;
}

.header {
  background-color: var(--clr-grey-light-1);
  grid-column: full-start/col-end 6;
}

.homes {
  background-color: aquamarine;
  grid-column: full-start/full-end;
}

.realtors {
  background-color: var(--clr-secondary);
  grid-column: col-start 7/full-end;
}

.sidebar {
  background-color: var(--clr-primary);
  grid-row: 1/-1;
}

.story__pictures {
  grid-column: full-start/col-end 4;
  background-image: linear-gradient(rgba(178, 132, 81, 0.5), rgba(178, 132, 81, 0.5)), url("../img/back.jpg");
  background-size: cover;
  background-position: center;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  align-items: center;
}

.story__img--1 {
  width: 100%;
  grid-column: 2/6;
  grid-row: 2/6;
}
.story__img--2 {
  width: 115%;
  grid-column: 4/7;
  grid-row: 4/6;
  z-index: 20;
}

.story__content {
  grid-column: col-start 5/full-end;
  padding: 6rem 8vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
}
.story__content .heading-2::first-letter {
  text-transform: capitalize;
}

.story__text {
  font-size: 1.6rem;
}

.container {
  display: grid;
  grid-template-rows: 80vh min-content 40vw repeat(3, min-content);
  grid-template-columns: [sidebar-start] 8rem [sidebar-end full-start] minmax(2rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 15rem) [col-end]) [center-end] minmax(2rem, 1fr) [full-end];
}

.btn {
  font-family: var(--font-display);
  border: none;
  border-radius: 0;
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  background-color: var(--clr-primary);
  color: #fff;
}
.btn:link, .btn:hover {
  background-color: var(--clr-primary-dark);
}

.mb-sm {
  margin-bottom: 2rem;
}

.mb-md {
  margin-bottom: 3rem;
}

.mb-lg {
  margin-bottom: 4rem;
}

.mb-hg {
  margin-bottom: 8rem;
}